.RecipeItem {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #0000007e;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    &__overlay {
        width: 70%;
        min-width: 768px;
        min-height: 50%;
        background-color: $bright-level1-color;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        padding: 20px;
        position: relative;
        border: 3px solid $dark-color;
        border-radius: 20px;
        &__title {
            font-size: $subtitle-font-size;
            width: 100%;
            padding: 20px 0 40px 0;
            text-align: center;
            border-bottom: 1px solid $dark-color; }
        &__section {
            width: 340px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 20px;
            border-right: 1px solid $dark-color;
            &__image {
                min-width: 300px;
                min-height: 300px; }
            &__table {
                margin-top: 20px;
                min-width: 300px;
                display: flex;
                flex-wrap: wrap;
                & h4 {
                    width: 100%;
                    text-align: center;
                    padding: 3px 0;
                    border: 2px solid $dark-color; }
                &__content {
                    display: flex;
                    width: 100%;
                    border: 1px solid $dark-color;
                    &__title, &__value {
                        width: 50%;

                        & p {
                            border: 1px solid $dark-color;
                            padding: 3px; } } } } }
        &__ingredients {
            width: calc(100% - 340px);
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;
            padding: 0 20px;
            & h3 {
                width: 100%;
                text-align: center;
                margin: 30px 0; }
            &__row {
                width: 100%;
                display: flex;
                border-bottom: 1px solid $dark-color;
                padding: 3px 0;
                &:last-child {
                    border: none; }
                &__title {
                    width: 70%; }
                &__weight {
                    width: 30%; } }
            &__link {
                text-decoration: none;
                color: $bright-level1-color;
                background-color: $main-color;
                font-weight: 400;
                letter-spacing: 1px;
                font-size: $normal-font-size;
                border: none;
                margin: 40px auto 0;
                padding: 10px 15px;
                transition: .3s;
                &:hover {
                    transform: translateY(10px) scale(1.1); } } }

        &__closeBtn {
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
            font-size: 40px;
            border: none;
            color: $main-color; } } }

@media (max-width: $tablet-width) {
    .RecipeItem__overlay {
        min-width: 100%;
        flex-direction: column;
        &__section {
            flex-direction: row;
            width: 100%;
            border: none;
            &__image {
                min-height: 200px;
                height: 200px;
                min-width: 200px;
                padding-right: 20px; }
            &__table {
                margin-top: 0; } }
        &__ingredients {
            width: 100%;
            & h3 {
                margin: 5px 0; } } } }
@media (max-width: 550px) {
    .RecipeItem__overlay__section__image {
        display: none; } }
@media (max-width: $phone-small-width) {
    .RecipeItem__overlay {
        &__section {
            flex-direction: column;
            &__image {
                display: block;
                margin-bottom: 20px; } }
        &__ingredients {
            & h3 {
                display: none; }
            &__row {
                display: none; } } } }




